export default {
	name: 'welcome-tip',
	data() {
		return {
			Content: "", Show: false, C_s: 5000,
		};
	},
	props: {
		content: {
			type: String,
			default: ""
		},
		show: {
			type: Boolean,
			default: false
		},
		c_s: {
			type: Number,
			default: 5000
		}
	},
	created() {
		this.Content = this.content;
		this.Show = this.show;
		this.C_s = this.c_s;
		this.iniTime();
	},
	watch: {
		content() {
			this.Content = this.content;
			if(!this.content) {
				this.Show = false;
			}
			this.iniTime();
		},
		show() {
			this.Show = this.show;
			this.iniTime();
		},
		c_s() {
			this.C_s = this.c_s;
			this.iniTime();
		}
	},
	methods: {
		iniTime() {
			if(this.Show && this.C_s > 0) {
				setTimeout(() => {
					this.Show = false;
					this.CloseEvent();
				}, this.C_s);
			}
		},
		CloseEvent() {
			this.$emit('closeFinish', this.Show);
		}
	}
}
